<template>
  <!-- 车辆信息 -->
  <div class="container">
    <div class="elStepsCon">
      <el-steps :active="active" finish-status="success">
        <el-step title="公司审核"></el-step>
        <el-step title="客服审核"></el-step>
        <el-step title="管理办审核"></el-step>
      </el-steps>
      <el-button style="margin-top: 12px" @click="next">下一步</el-button>
    </div>
    
  </div>
</template>

<script>
export default {
  name: "carInfo",
  data() {
    return {
      active: 0,
    };
  },
  methods: {
    next() {
      if (this.active++ > 2) this.active = 0;
    },
  },
};
</script>
<style scoped>
.container {
  background-color: #fff;
  height: 100%;
  padding: 20px 0;
}
.elStepsCon {
  width: 400px;
  margin: 20px auto;
}
.operating {
  margin-left: 20px;
}
.carInfo {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}
.carInfo .item {
  width: 50%;
  padding: 10px 0;
  font-size: 15px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.itemAll {
  width: 60% !important;

}
.itemAll  img{
  width: 150px;
  height: 150px;
}
.carInfo .item div {
   width: 20.33333333%;
   font-size: 15px;
}
span {
  color: #999;
}
</style>


